import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Route, Switch } from 'react-router-dom';
import Work from './components/work.js';
import Ideas from './components/ideas.js';
import Ventures from './components/ventures.js';
import Resume from './components/resume.js';
import Dreamhome from './components/dreamhome.js';
import Peertopeer from './components/peertopeer.js';
import Stjudeweb from './components/stjudeweb.js';
import Enterprise from './components/enterprise.js';
import Connectedroom from './components/connectedroom.js';
import Paytoplay from './components/paytoplaysites.js';
import Hiltonweb from './components/hiltonweb.js';
import Hiltonapps from './components/hiltonapps.js';
import Hiltonfractal from './components/hiltondesignsystem.js';
import Nav from './components/nav.js';
import ScrollToTop from './components/scrolltotop.js';
import './styles/App.css';


function App() {
  return (
    <HashRouter>
      <ScrollToTop />
    <Nav />
    <section id="main-content">
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/work" component={Work} />
        <Route path="/ideas" component={Ideas} />
        <Route path="/ventures" component={Ventures} />
        <Route path="/resume" component={Resume} />
        <Route path="/dreamhome" component={Dreamhome} />
        <Route path="/peertopeer" component={Peertopeer} />
        <Route path="/stjudeweb" component={Stjudeweb} />
        <Route path="/enterprise" component={Enterprise} />
        <Route path="/connectedroom" component={Connectedroom} />
        <Route path="/specialitysites" component={Paytoplay} />
        <Route path="/hilton-website" component={Hiltonweb} />
        <Route path="/hilton-design-system" component={Hiltonfractal} />
        <Route path="/hilton-apps" component={Hiltonapps} />
      </Switch>
    </section>
    </HashRouter>
  );
}

const Home = () => (
<div className="contentBody">
 <h2>Hey Yall</h2>
 <p>some content about</p>
 <ul>
  <li>for Design Leadership and Relationships</li>
  <li>culture, values, and process</li>
  <li>Design at Scale</li>
  <li>Design Systems</li>
  <li>Communicating the value of design and research</li>
 </ul>
</div>
);


export default App;
